@import url('https://fonts.googleapis.com/css2?family=Esteban&display=swap');

body {
	font-family: 'Esteban', serif;
}

.flex-container {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.flex-content {
  flex: 1 1 auto;
}

.fade-in {
  animation: fadeIn ease 3s;
}

.clickable {
  cursor: pointer;
}

.hyperlink {
  text-decoration: none;
  color: black;
}

@keyframes fadeIn{
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}