.post-list {
  margin: 1em 0;
  list-style: none;
}
.post-list:hover .post-link, .post-list:focus-within .post-link {
  opacity: 0.6;
}

.post-list li {
  margin-bottom: 0em;
}

.post-link {
  color: var(--text-color);
  display: flex;
  flex-direction: column-reverse;
  text-decoration: none;
  padding: 0.25em;
  transition: opacity 0.125s ease-in-out;
}
.post-link:hover, .post-link:focus {
  opacity: 1 !important;
}
