.post-date {
  color: darkgray
}
.post-link__heading {
  display: block;
  margin-right: 4em;
}
@media (min-width: 768px) {
  .post-link__heading {
    padding: 0.25em 0;
 }
}
.post-link__heading h1 {
  font-size: 1em;
  margin-bottom: 0;
  margin-top: 0;
}

.post-link__title + .post-link__excerpt {
  opacity: .6;
}

.post-pagination {
  margin-top: 4rem;
  font-size: 0.75em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center;
}
.post-pagination > * {
  flex-grow: 1;
  flex-basis: 0;
  padding: 1em 0;
}
.post-pagination:hover a, .post-pagination:focus-within a {
  opacity: 0.6;
}
.post-pagination a {
  text-decoration: none;
  text-transform: uppercase;
  transition: opacity 0.125s ease-in-out;
}
.post-pagination a:hover, .post-pagination a:focus {
  opacity: 1;
}

.post-indent {
  margin-left: 2em;
}