.book-img {
  max-height: 100vh;
  max-width: 100vw;
}

@keyframes glowing {
  0% {
    background-color: #e9d9a4;
    box-shadow: 0 0 3px #e9d9a4;
  }
  50% {
    background-color: #bd935c;
    box-shadow: 0 0 10px #bd935c;
  }
  100% {
    background-color: #e9d9a4;
    box-shadow: 0 0 3px #e9d9a4;
  }
}

.button-fade-in {
  animation: fadeIn ease 4s;
}

.glow {
  animation: glowing 3000ms infinite;
}